body {
  font-family: $font-sans-serif;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.4px;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

main {
  // max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
.container {
  padding: 0 15px;
  margin: 0 auto;
  max-width: 1400px;
}

.section-title {
  font-family: $font-serif;
  font-weight: 400;
  font-size: 45px;
  line-height: 1.3;
}

.section-text {
  font-size: 20px;
  line-height: 1.6;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overflow-hidden {
  overflow: hidden;
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /*       ; 
  ,        */
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

.container {
  min-width: $min-width-var;
  @include mq(tablet) {
    width: 768px;
  }
  @include mq(desktop) {
    width: 1440px;
  }
}
